<template>
<div class="cms-page-applist cms-p-list cms-user-list">
  <div class="cms-i-nav">
    <div class="nav-title el-icon-location-outline"></div>
    <div class="button-group fr">
      <el-button class='' type="success" round size="mini" @click="handleEditUser(-1, {}, true)"></el-button>
      <el-button class='' type="warning" round plain size="mini" icon="el-icon-download
" @click='exportCurPageExcel'></el-button>
      <el-button class='' type="danger" round plain size="mini" icon="el-icon-download
" @click='exportAllExcel'></el-button>
    </div>
  </div>
  <!--  -->
  <div class="search-box">
    <el-form :inline="true" class="demo-form-inline" size='mini'>
      <el-form-item>
        <el-input placeholder="" v-model="searchProps.username" clearable maxlength='50'></el-input>
      </el-form-item>
      <el-form-item>
        <el-input placeholder="" v-model="searchProps.phone" clearable maxlength='50'></el-input>
      </el-form-item>
      <el-form-item>
        <el-input placeholder="" v-model="searchProps.email" clearable maxlength='50'></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-select filterable v-model="searchProps.group" placeholder="">
          <el-option
            v-for="item in groupList"
            :key="item.value"
            :label="item.label"
            :value="item.value">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="">
        <el-select filterable v-model="searchProps.isban" placeholder="">
          <el-option
            v-for="item in isBanList"
            :key="item.value"
            :label="item.label"
            :value="item.value">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="">
        <el-select filterable v-model="searchProps.show_contact" placeholder="">
          <el-option
            v-for="item in showContactList"
            :key="item.value"
            :label="item.label"
            :value="item.value">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="">
        <el-select filterable v-model="searchProps.show_market" placeholder="APP" width='300px'>
          <el-option
            v-for="item in showMarketList"
            :key="item.value"
            :label="item.label"
            :value="item.value">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" icon="el-icon-search" @click="searchUserList"></el-button>
      </el-form-item>
    </el-form>
  </div>

  <!-- app -->
  <el-table class="app-table"
    ref="mainTable" border
    :data="listData" align='center'
    tooltip-effect="dark"
    style="width: 100%; text-align:center;">
    <el-table-column prop="UserName" label="" width="120" align='center' sortable show-overflow-tooltip>
      <template slot-scope="scope">
        <div class='t-username' @click="handleShowUserDetail(scope.$index, scope.row)">{{scope.row.UserName}}</div>
      </template>
    </el-table-column>
    <el-table-column prop="NickName" label="" align='center' sortable show-overflow-tooltip></el-table-column>
    <el-table-column prop="GroupId" label="" align='center'
      :filters="groupFilter"
      :filter-method="filterTag">
      <template slot-scope="scope">
        <el-tag size="mini" v-if="scope.row.GroupId"
          :type="scope.row.GroupId == 2 ? 'primary' : scope.row.GroupId == 1 ? 'danger' :'success'"
          close-transition>{{scope.row.GroupName}}</el-tag>
      </template>
    </el-table-column>
    <el-table-column prop="Email" label="" align='center' sortable show-overflow-tooltip></el-table-column>
    <el-table-column prop="Phone" label="" align='center' sortable show-overflow-tooltip></el-table-column>
    <el-table-column prop="QQ" label="QQ" align='center' sortable show-overflow-tooltip></el-table-column>
    <el-table-column prop="IsBan" label="" align='center'
      :filters="[{ text: '', value: 1 }, { text: '', value: 0 }]"
      :filter-method="filterTag">
      <template slot-scope="scope">
        <el-tag size="small"
          :type="scope.row.IsBan ? 'danger' :'success'"
          close-transition>{{scope.row.IsBan ? '' : ''}}</el-tag>
      </template>
    </el-table-column>
    <el-table-column prop="ShowContact" label="" align='center'
      :filters="[{ text: '', value: 1 }, { text: '', value: 0 }]"
      :filter-method="filterTag">
      <template slot-scope="scope">
        <el-tag size="small"
          :type="scope.row.ShowContact ? 'success' :'info'"
          close-transition>{{scope.row.ShowContact ? '' : ''}}</el-tag>
      </template>
    </el-table-column>
    <el-table-column prop="ShowMarket" label="App" align='center'
      :filters="[{ text: '', value: 1 }, { text: '', value: 0 }]"
      :filter-method="filterTag">
      <template slot-scope="scope">
        <el-tag size="small"
          :type="scope.row.ShowMarket ? 'success' :'info'"
          close-transition>{{scope.row.ShowMarket ? '' : ''}}</el-tag>
      </template>
    </el-table-column>
    <el-table-column label="" width="115" align='center'>
      <template slot-scope="scope">
      <el-tooltip effect="dark" content="">
        <el-button type="primary" icon="el-icon-edit" circle plain
          @click="handleEditUser(scope.$index, scope.row)"></el-button>
      </el-tooltip>
      <el-tooltip effect="dark" content="" v-if="!scope.row.IsBan">
        <el-button type="warning" icon="el-icon-remove-outline" circle plain
          @click="handleBanUser(scope.$index, scope.row)"></el-button>
      </el-tooltip>
      <el-tooltip effect="dark" content="" v-if="scope.row.IsBan">
        <el-button type="info" icon="el-icon-check" circle plain
          @click="handleUnBanUser(scope.$index, scope.row)"></el-button>
      </el-tooltip>
      <el-tooltip effect="dark" content="">
        <el-button type="danger" icon="el-icon-delete" circle plain
          @click="handleDeleteUser(scope.$index, scope.row)"></el-button>
      </el-tooltip>
      </template>
    </el-table-column>
  </el-table>

  <!--  -->
  <div class='i-pagination clearfix'>
    <div class='pagination-info fl'>
    <span class='num'> {{totalRecordNum}}</span>  <span class='num'>{{perPageRecordNum}}</span> <span class='num'>{{totalPage}}</span> <span class='num'>{{curPage}}</span> </div>
    <el-pagination class='fr'
      background
      @current-change="handlePaginationPageChange"
      layout="prev, pager, next, jumper" :current-page="1"
      :page-count="totalPage||1" :pager-count="5">
    </el-pagination>
  </div>

  <!--  -->
  <el-dialog class='i-appmodify-pop'
    :title="isCreateUser ? '' : ''" top="8vh"
    :before-close="handlerCloseModifyPop"
    :visible.sync="isShowEditDialog"
    width="380" center>
    <el-form ref="modifyForm" :model="editingInfo" label-position="left" label-width="110px"
      auto-complete="off" size='mini' :rules="userRules">
      <el-form-item label="" required>
        <el-input v-model="editingInfo.UserName" placeholder='' clearable
          :disabled="!isCreateUser" auto-complete="off"></el-input>
      </el-form-item>
      <el-form-item label="" :required="isCreateUser">
        <el-input v-model="editingInfo.Password" clearable auto-complete="off"
          placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="" required>
        <el-input v-model="editingInfo.NickName" placeholder='' clearable></el-input>
      </el-form-item>
      <el-form-item label="" required>
        <el-select filterable v-model="editingInfo.GroupId" placeholder="">
          <el-option
            v-for="(item, index) in groupList"
            v-if="index!=0"
            :key="item.value"
            :label="item.label"
            :value="item.value">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="" prop="Email">
        <el-input v-model="editingInfo.Email" placeholder='' clearable></el-input>
      </el-form-item>
      <el-form-item label="" prop="Phone">
        <el-input v-model="editingInfo.Phone" placeholder='' clearable></el-input>
      </el-form-item>
      <el-form-item label="QQ" prop="QQ">
        <el-input v-model="editingInfo.QQ" placeholder='QQ' clearable></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-switch v-model="editingInfo.IsBan" :active-value="1" :inactive-value="0"></el-switch>
      </el-form-item>
      <el-form-item label="">
        <el-switch v-model="editingInfo.ShowContact" :active-value="1" :inactive-value="0"></el-switch>
      </el-form-item>
      <el-form-item label="App">
        <el-switch v-model="editingInfo.ShowMarket" :active-value="1" :inactive-value="0"></el-switch>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="editingInfo.Remark" type="textarea" :rows="2"></el-input>
      </el-form-item>
    </el-form>
    <span slot="footer" class="dialog-footer">
      <el-button @click="isShowEditDialog=false"> </el-button>
      <el-button type="primary" @click="submitEditInfo"> </el-button>
    </span>
  </el-dialog>

  <!--  -->
  <el-dialog class='i-appmodify-pop'
    title="" :top="'10vh'"
    :visible.sync="isShowDetailDialog"
    width="380" center>
    <div class='clearfix'>
      <el-button type="danger" icon="el-icon-edit" size='mini' class='fr'
        style="margin:-9px 0 5px;" @click="handleEditUser(detailInfo._$index, detailInfo)"></el-button>
    </div>
    <ul class='i-appmodify-list'>
      <li><span class='i-m-tag'></span><span class='i-m-val'>{{detailInfo.UserName}}</span></li>
      <li><span class='i-m-tag'></span><span class='i-m-val'>{{detailInfo.NickName}}</span></li>
      <li><span class='i-m-tag'></span><span class='i-m-val'>{{detailInfo.Password}}</span></li>
      <li><span class='i-m-tag'></span><span class='i-m-val'>{{detailInfo.GroupName}}</span></li>
      <li><span class='i-m-tag'></span><span class='i-m-val'>{{detailInfo.Email}}</span></li>
      <li><span class='i-m-tag'></span><span class='i-m-val'>{{detailInfo.Phone}}</span></li>
      <li><span class='i-m-tag'>QQ</span><span class='i-m-val'>{{detailInfo.QQ}}</span></li>
      <li><span class='i-m-tag'></span><span class='i-m-val'>{{detailInfo.IsBan? '' : ''}}</span></li>
      <li><span class='i-m-tag'></span><span class='i-m-val'>{{detailInfo.ShowContact==1? '' : ''}}</span></li>
      <li><span class='i-m-tag'>App</span><span class='i-m-val'>{{detailInfo.ShowMarket==1? '' : ''}}</span></li>
      <li><span class='i-m-tag'></span><span class='i-m-val'>
        {{detailInfo.CreateTime ? new Date(detailInfo.CreateTime).toLocaleDateString() : ''}}
      </span></li>
      <!-- <li><span class='i-m-tag'></span><span class='i-m-val'>
        {{detailInfo.LoginTime ? new Date(detailInfo.LoginTime).toLocaleDateString() : ''}}
      </span></li> -->
    </ul>
  </el-dialog>
</div>
</template>

<script>
export default {
  name: 'UserList',
  data () {
    let validateRegexpFn = (dataType, valName, required) => {
      return function(rule, value, callback) {
        console.log(`value: ${value}, valName: ${valName}, dataType: ${dataType}, required: ${required}`);
        if (value === '') {
          !required && callback();
          required && callback(new Error('' + valName));
        } else if (!window.$myCommon.testValid(value, dataType)) {
          callback(new Error('' + valName));
        } else {
          callback();
        }
      };
    };

    return {
      editingInfo: { // 
        UserName: '',
        Password: '',
        Email: '',
        Phone: '',
        QQ: '',
        NickName: ''
      },
      detailInfo: {}, // 
      modifyForm: {
        UserName: '',
        Password: '',
        Email: '',
        Phone: '',
        QQ: '',
        NickName: ''
      },

      curPage: 1, // 
      totalPage: 1, // 
      totalRecordNum: 0, // 
      perPageRecordNum: 10, // 

      isShowEditDialog: false, // App
      isShowDetailDialog: false, // App

      isCreateUser: false,
      editingInfoIndex: -1,

      searchProps: { // 
        username: '',
        phone: '',
        email: '',
        group: '',
        status: '',
        isban: '',
        show_contact: '',
        show_market: ''
      },
      groupList: [],
      groupFilter: [],
      isBanList: [
        {label: '', value: null},
        {label: '', value: 0},
        {label: '', value: 1}
      ],
      showContactList: [
        {label: '', value: null},
        {label: '', value: 1},
        {label: '', value: 0}
      ],
      showMarketList: [
        {label: '', value: null},
        {label: '', value: 1},
        {label: '', value: 0}
      ],
      listData: [],
      userRules: {
        Email: [
          { validator: validateRegexpFn('email', '', false), trigger: 'blur' }
        ],
        Phone: [
          { validator: validateRegexpFn('phone', '', false), trigger: 'blur' }
        ],
        QQ: [
          { validator: validateRegexpFn('qq', 'QQ', false), trigger: 'blur' }
        ]
      }
    };
  },
  watch: {
    '$route' (to, from) {
      if(to.name == 'addUser') { // 
        this.handleEditUser(-1, {}, true);
      }
    }
  },
  methods: {
    filterTag(value, row, column) { // 
      return row[ column['property'] ] == value;
    },
    handlePaginationPageChange(val) { // 
      this.curPage = val;
      this.getUserList();
    },
    handleShowUserDetail(index, row) { // 
      row._$index = index;
      this.detailInfo = Object.assign({}, row); // 
      this.isShowDetailDialog = true;
    },
    handleEditUser(index, row, isCreateUser) { // app
      this.isCreateUser = !!isCreateUser;
      this.$refs.modifyForm && this.$refs.modifyForm.resetFields(); // 
      if(isCreateUser) { // App
        row.GroupId = window.$store.state.GroupMaps.custom.id;
      } else {
        this.editingInfoIndex = index;
      }
      row.GroupId = parseInt(row.GroupId || 0);
      this.editingInfo = Object.assign({}, row); // 
      this.isShowEditDialog = true;
    },
    submitEditInfo() { // app
      let userInfo = this.editingInfo;
      // let pwd = !userInfo.Password ? null : CryptoJS.AES.encrypt(userInfo.Password, window.$myConfig.PWD_CRYPTO_SALT).toString();
      let info = {
        username: userInfo.UserName,
        nickname: userInfo.NickName,
        pwd: userInfo.Password,
        group_id: userInfo.GroupId,
        email: userInfo.Email,
        phone: userInfo.Phone,
        qq: userInfo.QQ,
        avatar: userInfo.Avatar,
        isban: userInfo.IsBan,
        show_contact: userInfo.ShowContact,
        show_market: userInfo.ShowMarket,
        remark: userInfo.Remark,
      };
      if(this.isCreateUser) {
        this._addUser(info);
      } else {
        info.id = userInfo.Id;
        this._editUser(info);
      }
    },
    _addUser(info, cb) { // 
      window.$cmsApi.createUser(info, (res) => {
        cb && cb(res);
        this.isShowEditDialog = false;
        this.$message({ message: '', type: 'success', showClose: true });
        let editingInfo = this._formatUserInfo(this.editingInfo);
        this.listData.unshift(editingInfo);
      }, (msg) => {
        window.$myCommon.toast(msg || '');
      });
    },
    _editUser(info, cb) { // 
      window.$cmsApi.updateUser(info, (res) => {
        cb && cb(res);
        this.isShowEditDialog = false;
        this.isShowDetailDialog = false;
        this.$message({ message: '', type: 'success', showClose: true });
        let editingInfo = this._formatUserInfo(this.editingInfo);
        this.listData.splice(this.editingInfoIndex, 1, editingInfo);
      }, (msg) => {
        window.$myCommon.toast(msg || '');
      });
    },
    handleDeleteUser(index, row) { // 
      this.$confirm(` ${row.UserName} ?`, '', {
        confirmButtonText: '',
        cancelButtonText: '',
        center: true
      }).then(() => {
        window.$cmsApi.deleteUser(row.Id, () => {
          this.$message({
            type: 'success',
            message: '!'
          });
          this.listData.splice(index, 1);
        }, (msg) => {
          window.$myCommon.toast(msg || '');
        });
      }).catch(() => {
      });
    },
    handleBanUser(index, row) { // 
      this.$confirm(` ${row.UserName} ?`, '', {
        confirmButtonText: '',
        cancelButtonText: '',
        center: true
      }).then(() => {
        window.$cmsApi.banUser(row.Id, () => {
          this.$message({
            type: 'success',
            message: '!'
          });
          row.IsBan = 1;
          row = this._formatUserInfo(row);
          this.listData.splice(index, 1, row);
        }, (msg) => {
          window.$myCommon.toast(msg || '');
        });
      }).catch((err) => {
        console.log(err);
      });
    },
    handleUnBanUser(index, row) { // 
      this.$confirm(` ${row.UserName} ?`, '', {
        confirmButtonText: '',
        cancelButtonText: '',
        center: true
      }).then(() => {
        window.$cmsApi.unBanUser(row.Id, () => {
          this.$message({
            type: 'success',
            message: ` ${row.UserName} `
          });
          row.IsBan = 0;
          this.listData.splice(index, 1, row);
        }, (msg) => {
          window.$myCommon.toast('');
        });
      }).catch(() => {
      });
    },
    handlerCloseModifyPop(done) { // 
      this.$router.push({ name: 'userList' });
      done();
    },
    searchUserList() {
      this.getUserList();
    },
    getSearchParams() {
      let searchProps = this.searchProps || {};
      let options = {
        pageSize: this.perPageRecordNum || 20,
        page: this.curPage || 1,
        usernme: searchProps.username,
        phone: searchProps.phone,
        email: searchProps.email,
        group_id: searchProps.group == 'all' ? '' : searchProps.group,
        // status: searchProps.status == 'all' ? '' : searchProps.status,
        isban: searchProps.isban == 'all' ? '' : searchProps.isban,
        show_contact: searchProps.show_contact,
        show_market: searchProps.show_market,
      };
      return options;
    },
    getUserList(cb) { // 
      let options = this.getSearchParams();
      window.$cmsApi.getUserList(options, (res={}) => {
        cb && cb(res);
        let usersData = res.data || {};
        let userCountData = usersData.count || {};
        this.listData = this._formatUserList(usersData.list || []);
        this.totalPage = userCountData.pageCount || 1;
        this.totalRecordNum = userCountData.totalCount || 0;
      }, (msg) => {
        window.$myCommon.toast(msg || '');
      });
    },

    _formatUserInfo(userInfo={}) {
      userInfo.IsBan = userInfo.IsBan !== undefined ? userInfo.IsBan
        : userInfo.Status == -2 ? 1 : 0;
      userInfo.GroupName = window.$myCommon.getGroupNameByGroupId(userInfo.GroupId);
      return userInfo;
    },

    _formatUserList(list=[]) {
      _.each(list, (item, index) => {
        item = this._formatUserInfo(item);
      });
      return list;
    },

    exportCurPageExcel() {
      let options = this.getSearchParams();
      window.$cmsApi.exportUserExcel(options);
    },
    exportAllExcel() {
      window.$cmsApi.exportUserExcel({
        pageSize: 1000,
        page: 1
      });
    },
    initGroupList() { // 
      let GroupMaps = window.$store.state.GroupMaps;
      this.groupList = [ {label: '', value: 'all'} ];
      this.groupFilter = [];
      _.each(GroupMaps, group => {
        this.groupList.push({ label: group.name, value: group.id });
        this.groupFilter.push({ text: group.name, value: group.id });
      });
    }
  },
  mounted() {
    this.mainTable = this.$refs.mainTable;
    window.$myCommon.checkLogin((data) => {
      this.getUserList();
      if(this.$route.name == 'addUser') { // App
        this.handleEditUser(-1, {}, true);
      }

      this.initGroupList(); // 
    }, () => {});

  }
}
</script>

<style scoped>
.cms-i-nav {
  height: 30px;
  line-height: 30px;
  padding-bottom: 10px;
  border-bottom: 1px solid #eee;
}
.batch-btns-box {
  padding: 12px 0 2px;
}
.batch-btns-box .el-button {
  margin-bottom: 10px;
}
.app-table .el-button.is-circle {
  padding: 5px;
}
.app-table .el-button+.el-button {
    margin-left: 2px;
}
.app-table .el-button--mini, .app-table .el-button--mini.is-round {
    padding: 5px 10px;
}
.t-username {
  color: #1f7eab;
  cursor: pointer;
}

.i-appmodify-pop .el-form-item--mini.el-form-item, .i-appmodify-pop .el-form-item--small.el-form-item {
    margin-bottom: 15px;
}
.i-appmodify-list li {
  padding: 7px 10px;
}
.i-appmodify-list li:nth-child(odd) {
  background: #f9f9f9;
}
.i-appmodify-list .i-m-tag {
  color: #777;
  width: 110px;
  display: inline-block;
}
.i-appmodify-list .i-m-val {
  color: #025d7f;
  font-weight: bold;
}
</style>



