<template>
<div class="cms-page-settings">
  <div class="cms-i-nav">
    <div class="nav-title el-icon-location-outline"></div>
  </div>

  <div class="s-panel s-panel-sql">
    <h3 class="s-panel-title"><i class="el-icon-sold-out"></i>. </h3>
    <div class="s-panel-con">
      <p> <span class="num"></span> </p>
      <p> <span class="num">D:\web\app_cms\backup_mysql</span> </p>
      <p> <span class="num">5</span> </p>
