      <p></p>
      <p>
        <el-button type="success" icon="el-icon-download" size="small" @click="downlaodSqlBackup"></el-button>
      </p>
    </div>
  </div>
</div>
</template>

<script>
export default {
  name: 'UserCenter',
  data () {
    return {

    }
  },
  methods: {
    downlaodSqlBackup() {
      window.open (window.API_ROUTER.backupSql, "_blank") ;
    }
  },
  mounted() {
    window.$myCommon.checkLogin((data) => { }, () => {});
  }
}
</script>

<style scoped>
.s-panel {
  padding: 20px;
  border-bottom: 1px solid #ccc;
}
.s-panel:last-child {
  border-bottom: none;
}
.s-panel-title {
  font-size: 18px;
  color: #0d7eb3;
  margin: 10px 0 15px;
}
.s-panel-title i {
  margin-right: 5px;
  font-weight: bold;
}
.s-panel-con {
  line-height: 1.8;
  font-size: 13px;
  color: #666;
}
.s-panel-con .num {
  color: #f36e6e;
  font-weight: bold;
}
</style>



